<template>
  <div class="top">
    <!-- -->
    <div class="center">
      <e-input
        v-model="SFZvalue"
        textAlign="left"
        placeholder=""
        type="idCard"
        mask="999999 99999999 999S"
        placeholderColor="#bec1c8"
        :maxlength="20"
        ><img class="input_icon icon_name" style="vertical-align: middle;"
        src=@/assets/svg/idCard.svg></e-input
      >
      <div class="active-button" @click="active()">{{ "" }}</div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import skeleton from "@/components/skeleton";
import eInput from "@/components/input";
import util, {
  clearParams,
  getRelationShip,
  getlastDay,
  getDay,
  getcheckDay,
  reconfigArr,
  checkIdCard,
} from "@/components/_utils";

export default {
  components: { eInput },
  computed: {
    ...mapState({
      columns: (state) => state.app.collection.columns,
    }),
  },
  data() {
    return {
      queryData: {},
      productList: [],
      mark: [],
      num: -1,
      SFZvalue: "",
      SFZage: "",
    };
  },
  methods: {
    active() {
      //  
      let Sfpd = util.checkIdCard(this.SFZvalue);
      if (Sfpd) {
        // this.$toast("");
        // 
        this.SFZage = util.getAge(this.SFZvalue);
        console.log(this.SFZage, "");
        this.init();
      } else {
        this.$toast("");
        // this.$dialog.alert({
        //   message: "",
        //   width: "60vw",
        //   confirmButtonText: "",
        //   className: "back-tip"
        // });
      }
    },
    init() {
      let obj = this.queryData;
      console.log(obj, "obj");
      var propsCurr = Object.keys(obj);
      this.productList.map((item, index) => {
        console.log(item, " this.productList.item");
        if (item.delete) {
          item.delete.map((i) => {
            console.log(i, "i");
            delete obj[i];
          });
          console.log(item.delete, "item.delete");
        }
        console.log(this.productList, "delete this.productList");
        this.mark = propsCurr.map((o) => {
          console.log(item, "ssssss");
          if (this.SFZage >= item.minage && this.SFZage <= item.maxage) {
            console.log("wwwwwwwwwwwwwwwww");
            if (obj[o] == item[o]) {
              return true;
            } else {
              return false;
            }
          } else {
            return false;
          }
        });
        console.log(this.mark, "this.mark");
        if (!this.mark.includes(false) && this.mark.length > 0) {
          this.num = index;
          console.log(index, "+++", this.productList[index]);
        }
      });
      this.compareObj();
    },
    compareObj() {
      if (this.num > -1) {
        sessionStorage.setItem("SFZvalue", this.SFZvalue);
        this.$router.replace({
          name: "card-info",
          params: {
            batchNo: this.productList[this.num].batchNo,
          },
          query: this.$store.state.app.routeQuery,
        });
      } else {
        skeleton.hide();
        this.$router.replace({
          name: "card-result",
          params: {
            type: "0",
            message: "",
          },
        });
      }
    },
  },
  created() {
    this.queryData = this.$route.query;
    this.productList = this.columns;
    sessionStorage.setItem("yntInfo", JSON.stringify(this.queryData));
    // this.init()
    console.log(this.queryData, "ynt");
    console.log(this.columns, "this.productList");
    console.log(
      this.$store.state.app.routeQuery,
      "this.$store.state.app.routeQuery"
    );
  },
};
</script>
<style lang="less" scoped>
@marginOfForm: 35.5vw;
@cardHeight: 12.5vw;
@gray: #efefef;
@font10: 2.7vw;
@font12: 3.2vw;
@font14: 3.7vw;
@font16: 4.2vw;
@font18: 4.8vw;
.active-button {
  margin-top: 10vh;
  flex: 2.5;
  line-height: 13vw;
  background: linear-gradient(to right, #4ec3ff, #3788ed);
  font-size: @font16;
  color: #fff;
}
.top {
  height: 100vh;
  display: flex;
  // justify-content: center;
  flex-direction: column;
}
.center {
  align-items: center;
}
</style>