<template>
  <div id="identify_box" @click.stop>
    <div class="box">
      <div class="close" @click="close"><img src=~@/assets/svg/close.svg></div>
      <div class="title"></div>
      <div class="phone"> {{phone}}</div>
      <input
        class="i-input"
        ref="refinput"
        type="tel"
        @blur="focus = false"
        @focus="focus = true"
        v-model="code"
        maxlength="6"
      >
      <div class="code" @click="$refs.refinput.focus()">
        <span
          :data-value="code[0]"
          :class="{active:focus || code[0],input:code.length === 0 && focus}"
        ></span>
        <span
          :data-value="code[1]"
          :class="{active:code[0]&&focus || code[1],input:code.length === 1 && focus}"
        ></span>
        <span
          :data-value="code[2]"
          :class="{active:code[1]&&focus || code[2],input:code.length === 2 && focus}"
        ></span>
        <span
          :data-value="code[3]"
          :class="{active:code[2]&&focus || code[3],input:code.length === 3 && focus}"
        ></span>
        <span
          :data-value="code[4]"
          :class="{active:code[3]&&focus || code[4],input:code.length === 4 && focus}"
        ></span>
        <span
          :data-value="code[5]"
          :class="{active:code[4]&&focus || code[5],input:code.length === 5 && focus}"
        ></span>
      </div>
      <div :class="['timeout',{clickable:time <= 0}]" @click="reSend">{{timeout}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "identify",
  props: {
    phone: String,
    length: {
      type: Number,
      default: 6
    }
  },
  data() {
    return {
      // 
      timemeter: "",
      time: 60,
      focus: false,
      // 
      code: ""
    };
  },
  methods: {
    reSend() {
      if (this.time > 0) return;
      this.code = "";
      this.time = 60;
      this.timemeter = setInterval(() => {
        if (this.time > 0) this.time--;
        if (this.time <= 0) clearInterval(this.timemeter);
      }, 1000);
      this.$refs.refinput.focus();
      this.$emit("getCaptcha");
    },
    close() {
      this.$emit("close");
    }
  },
  mounted() {
    this.timemeter = setInterval(() => {
      if (this.time > 0) this.time--;
      if (this.time <= 0) clearInterval(this.timemeter);
    }, 1000);
  },
  computed: {
    timeout() {
      return this.time > 0 ? this.time + " " : "";
    }
  },
  watch: {
    code(val) {
      if (val.length === 6) {
        this.$emit("checkCaptcha", val);
      }
    }
  }
};
</script>

<style lang='less' scoped>
#identify_box .box {
  width: 80vw;
  height: 55vw;
  margin: 0 auto;
  overflow: hidden;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 10px;
  padding: 7vw 3vw 0;
  box-sizing: border-box;
  .close {
    position: absolute;
    text-align: center;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
  }
  .title {
    text-align: left;
    font-size: 5vw;
    font-weight: 500;
    color: #303133;
  }
  .phone {
    text-align: left;
    font-size: 3.7vw;
    color: #bec1c8;
    padding: 10px 0;
    .second {
      text-align: center;
    }
  }
  .i-input {
    width: 100%;
    height: 60px;
    display: block;
    position: absolute;
    left: 0;
    top: 95px;
    opacity: 0;
  }
  .code {
    text-align: center;
    font-size: 0px;
    padding-top: 16px;
    display: flex;
    span {
      border: 1px solid #efefef;
      width: 10vw;
      height: 10vw;
      // line-height: 10vw !important;
      color: #303133;
      font-size: 5vw;
      font-weight: 500;
      margin: 0 auto;
      position: relative;
      &::before {
        content: attr(data-value);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &:first-child {
        margin-left: 0;
      }
      &:last-child {
        margin-right: 0;
      }
      &.input {
        &::after {
          content: "";
          position: absolute;
          width: 2px;
          height: 6vw;
          background-color: #000;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          animation: input 1.5s infinite;
        }
      }
      &.active {
        border: 1px solid #303133;
      }
    }
  }
  .timeout {
    margin-top: 7vw;
    font-size: 3.2vw;
    color: #c9c9c9;
    text-align: left;
    &.clickable {
      color: #4ca2fe;
    }
  }
}
@keyframes input {
  0%,
  100% {
    background-color: #000;
  }
  50% {
    background-color: #fff;
  }
}
</style>
