<template>
  <div>
    <div class="ticket_buyer_info">
      <div class="ticket_buyer_info_title">
        <div class="ticket_buyer_info_title_text">
          {{ infoData.data.id }}
        </div>
        <!-- <div class="ticket_buyer_info_title_err">
              <img src="@/assets\image\shaanxi-skiing/info.png" alt="" />
              <div></div>
            </div> -->
        <!-- <div class="ticket_buyer_info_title_err2">
              <van-icon color="#24B36E" name="checked" />
              <div style="margin-left: 1vw"></div>
            </div> -->
        <div class="ticket_buyer_info_title_err3">
          <van-icon color="#FF0000" name="clear" />
          <div style="margin-left: 1vw"></div>
        </div>
      </div>
      <div class="info_personnel_div">
        <div class="info_personnel_all">
          <div class="ticket_buyer_info_personnel">
            <div class="ticket_buyer_info_personnel_left">
              <div class="ticket_buyer_info_personnel_left_name">
                <div class="personnel_left_name_div1">
                  <div class="personnel_left_name_div1_name">
                    {{ infoData.data.name }}
                  </div>
                  <div class="personnel_left_name_div1_phone">
                    {{ infoData.data.phone }}
                  </div>
                </div>
                <div class="personnel_left_name_div2">
                  {{ infoData.data.price }}
                </div>
              </div>

              <div class="ticket_buyer_info_personnel_left_name">
                <div class="personnel_left_name_div1">
                  <div class="personnel_left_name_div1_name"></div>
                  <div class="personnel_left_name_div1_phone">
                    {{ infoData.data.idCard }}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="ticket_buyer_info_personnel_right">
            <van-checkbox
              v-model="infoData.data.checked"
              shape="square"
            ></van-checkbox>
          </div>
        </div>
        <div class="policyholder">
          <div class="policyholder_title">
            <div class="policyholder_title_left">
              <div></div>
              <div class="ticket_buyer_info_title_err">
                <img src="@/assets\image\shaanxi-skiing/info.png" alt="" />
                <div></div>
              </div>
            </div>
            <div
              class="policyholder_title_right"
              @click="selectPolicyMethod(infoData.data, dataIndex)"
            >
              
            </div>
          </div>
        </div>
        <div class="policyholder_form">
          <van-form @submit="onSubmit" ref="form" @failed="onFailed">
            <van-field
              v-model="infoData.form.name"
              name="userName"
              maxlength="20"
              label=""
              placeholder=""
              :disabled="infoData.form.disabled"
              :rules="[
                { required: true, message: '' },
                { validator: userNameValidator, message: '' },
              ]"
            >
            </van-field>
            <!--  -->
            <van-field
              v-model="infoData.form.phone"
              :disabled="infoData.form.disabled"
              name="phone"
              label=""
              placeholder=""
              type="digit"
              maxlength="11"
              :rules="[
                { required: true, message: '' },
                {
                  validator: phoneValidator,
                  message: '',
                },
              ]"
            />

            <van-field
              v-model="infoData.form.idCard"
              name="idCard"
              label=""
              :disabled="infoData.form.disabled"
              maxlength="18"
              placeholder=""
              :rules="[
                { required: true, message: '' },
                {
                  validator: idCardValidator,
                  message: '',
                },
              ]"
            />

            <!--  -->
            <van-field
              v-if="false"
              readonly
              clickable
              :disabled="infoData.form.disabled"
              name="ChoosePlan"
              :value="infoData.form.ChoosePlan"
              label=""
              placeholder=""
              :rules="[{ required: true, message: '' }]"
              @click="showChoosePlanMeth"
            >
              <template #button>
                <span class="chioce" v-if="!infoData.form.ChoosePlan"
                  >
                </span>
                <van-icon
                  v-if="!infoData.form.ChoosePlan"
                  color="#4ca2fe"
                  style="vertical-align: middle"
                  size="20px"
                  name="arrow"
                />
                <van-icon
                  v-if="infoData.form.ChoosePlan"
                  style="vertical-align: middle"
                  size="20px"
                  name="arrow"
                />
              </template>
            </van-field>
            <van-popup v-model="showChoosePlan" position="bottom">
              <van-picker
                show-toolbar
                :columns="columnsChoosePlan"
                @confirm="onConfirmChoosePlan"
                @cancel="showChoosePlan = false"
              />
            </van-popup>
          </van-form>
        </div>
      </div>
    </div>
    <!--  -->
    <selectPolicyholder
      ref="selectPolicyholder"
      @updateSelectedPerson="updateSelectedPerson"
    ></selectPolicyholder>
  </div>
</template>

<script>
import selectPolicyholder from "../../components/selectPolicyholder/index.vue";
import util from "@/components/_utils";
export default {
  components: { selectPolicyholder },
  props: {
    infoData: {
      type: Object,
      required: true,
    },
    dataIndex: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      // 
      showChoosePlan: false,
      columnsChoosePlan: [],

      //   infoData: {
      // form: {
      //   name: "*1",
      //   idCard: "3429************5451",
      //   phone: "15123456789",
      //   idType: "1010",
      //   ChoosePlan: "ss",
      // },
      // data: {
      //   id: "1",
      //   price: "65",
      //   name: "*  ",
      //   idCard: "3429************5456",
      //   phone: "15123456781",
      //   checked: true,
      // },
      //   },

      isonSubmit: false,
    };
  },
  mounted() {},
  methods: {
    // 
    showChoosePlanMeth() {
      console.log(this.infoData);

      if (!this.infoData.form.disabled) {
        this.showChoosePlan = true;
      }
    },
    updateSelectedPerson(selectedPerson) {
      console.log("", selectedPerson);
      //   this.ticketBuyerData[this.selectPolicyIndex].form = selectedPerson;

      this.$emit("infoSelectedPerson", {
        selectedPerson,
        selectPolicyIndex: this.selectPolicyIndex,
      });
      // 
    },
    // 
    selectPolicyMethod(item, index) {
      this.selectPolicyIndex = index;
      this.$refs.selectPolicyholder.showPopup = true;
    },
    onSubmit(va) {
      console.log(va);
      this.isonSubmit = true; //  true
      this.$emit('form-submit', this.infoData.form); // 
    },
    onFailed(errorInfo) {
      console.log("failed", errorInfo);
      this.isonSubmit = false; //  false
    },
    handleSubmitClick() {
      console.log("failed");
    },
    // 
    userNameValidator(e) {
      console.log(e);
      return util.checkName(e);
    },
    // 
    idCardValidator(e) {
      return util.checkIdCard(e);
    },
    // 
    phoneValidator(e) {
      return /^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(e);
    },
    // 
    onConfirmChoosePlan(value, index) {
      console.log("", value, index);
      this.showChoosePlan = false;
    },
  },
  computed: {},
  watch: {},
};
</script>
<style lang="less" scoped>
.ticket_buyer_info {
  margin-top: 3.3vw;
  display: flex;
  flex-direction: column;
}
.info_personnel_all {
  display: flex;
  width: 100%;
}

.ticket_buyer_info_personnel_right {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 3vw;
}
.ticket_buyer_info_title {
  display: flex;
}

.ticket_buyer_info_title_text {
  color: #979797;
  font-size: 3vw;
  width: 15%;
}

.ticket_buyer_info_title_err {
  margin-left: 3vw;
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    width: 3vw;
    height: 3vw;
  }

  div {
    font-size: 3vw;
    color: #ff0000;
    margin-left: 2px;
  }
}

.ticket_buyer_info_title_err2 {
  margin-left: 3vw;
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ddfaec;
  padding: 1vw;
  border-radius: 6px;
  font-size: 3vw;
}

.ticket_buyer_info_title_err3 {
  border-radius: 6px;
  margin-left: 3vw;
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fce9e9;
  padding: 1vw;
  font-size: 3vw;
}

.ticket_buyer_info_personnel {
  margin-top: 2vw;
  padding: 2vw 3vw;
  background: #f6f8fb;
  border-radius: 7px;
  display: flex;
  width: 100%;
}

.ticket_buyer_info_personnel_left {
  display: flex;
  flex-direction: column;
  width: 100%;

  .ticket_buyer_info_personnel_left_name {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.personnel_left_name_div1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1vw;
  font-size: 4vw;
}

.personnel_left_name_div1_name {
  font-size: 4vw;
}

.personnel_left_name_div1_phone {
  font-size: 3.7vw;
  margin-left: 1vw;
}
.info_personnel_div {
  display: flex;
  flex-direction: column;
}
.policyholder {
  display: flex;
  flex-direction: column;
  margin-top: 3vw;
}

.policyholder_title {
  display: flex;
  justify-content: space-between;
}

.policyholder_title_left {
  font-size: 4vw;
  color: #666666;
  display: flex;
}

.policyholder_title_right {
  font-size: 3vw;

  color: #4d71cd;
}

.policyholder_form {
  margin-top: 3vw;
}

