<template>
  <div class="e-date-picker">
    <e-input
      disabled
      @click.native="!disabled&&(showPicker=true)"
      v-bind="$attrs"
      :value="currentText"
      :placeholder="placeholder"
      :isLink="!disabled"
      
    >
      <slot></slot>
    </e-input>
    <van-popup v-model="showPicker" position="bottom">
      <van-datetime-picker v-bind="$attrs" @confirm="confirm" @cancel="cancel"></van-datetime-picker>
    </van-popup>
  </div>
</template>

<script>
import eInput from "@/components/input";
export default {
  name: "date-picker",
  components: {
    eInput
  },
  data() {
    return {
      currentValue: "",
      currentText: "",
      showPicker: false,
      isLink:true
    };
  },
  props: {
    value: {
      type: [Object, String, Number]
    },
    placeholder: {
      type: String,
      default: ""
    },
    disabled:Boolean
  },
  watch: {
    value(val) {
      this.currentValue = val;
    },
    currentValue(val) {
      this.currentText = val === "2099-12-31" ? "" : val;
      this.$emit("input", val);
    }
  },
  methods: {
    confirm(payLoad) {
      const date = new Date(payLoad);
      let YYYY = date.getFullYear(),
        MM = date.getMonth() + 1,
        DD = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      this.currentValue = YYYY + "-" + (MM < 10 ? "0" + MM : MM) + "-" + DD;
      this.showPicker = false;
    },
    cancel() {
      this.showPicker = false;
    }
  },
  created() {
    if (this.value) {
      this.currentValue = this.value;
    }
  }
};
</script>

<style lang="less" scoped>
/deep/ .e-input-com .e-input-view {
  align-items: center;
  &::after {
    display: none;
    visibility: hidden;
  }
}
</style>
