    wordTip() {
      const word = this.pdfs[this.pdfIndex];
      const enrc = this.ENCR;
      const plans = this.productData.plans;
      if (word && word.notPdf) {
        const planCodes = plans.map((v) => v.planCode.slice(0, -2));
        const risks = plans.map(
          (v) =>
            enrc.find((item) => item.risk === v.planCode.slice(0, -2)).riskName
        );
        const HI = enrc
          .filter((v) => planCodes.includes(v.risk) && v.isHI)
          .sort((a, b) => planCodes.indexOf(a.risk) - planCodes.indexOf(b.risk))
          .map((v) => v.riskName);
        if (word.type === "exoneration") {
          return `${risks.join("")}${
            word.materialName
          }`;
        } else {
          console.log("HI", HI);
          const HItip =
            HI.length > 0 ? `${HI.join("")}` : "";
          return `${risks.join("")}${HItip}${
            word.materialName
          }`;
        }
      } else {
        return "";
      }
    },
    // 
    insuranceDetail(){
     let detail=this.productData.prdInsuranceDutyList.some(item=>
     item.dutyDetails&&item.dutyDetails!='')
     return detail
    }
    
