<template>
  <div class="view-box" v-show="boxShow">
    <label class="input-box" :style="{'--color':color}">
      <span :class="{span:focus}"></span>
      <input v-model="cardNo" type="text" @focus="focus=true" @blur="blur" @input="input" />
    </label>
  </div>
</template>

<script>
import util from "@/components/_utils";
import { mapActions } from "vuex";
import mask from "vanilla-masker";

export default {
  data() {
    return {
      cardNo: "",
      focus: false,
      color:"#4ca2fe",
      boxShow:true
    };
  },
  components: {
    // eInput
  },
  created() {
    if (!this.$route.params.batchNo) {
      this.$router.replace({
        name: "card-result",
        params: {
          type: "0",
          message: ""
        }
      });
    }
  },
  watch: {
    cardNo(val) {
      const cardNo = val.replace(/\s/g, "")
      if ( cardNo.length === 18) {
        if(!util.checkIdCard(cardNo)) return this.color = "#d82645";
        this.cardValidationCretNo({
          batchNo: this.$route.params.batchNo, //
          plchdCrdtNo: this.cardNo.toUpperCase().replace(/\s+/g, ""), //
          channelCode: this.$store.state.app.channelCode
        });
      }
    }
  },
  methods: {
    ...mapActions(["cardValidationCretNo"]),
    blur() {
      if(!this.cardNo) this.focus = false;
    },
    input() {
      this.color = "#4ca2fe"
      this.cardNo = mask.toPattern(this.cardNo, "999999 99999999 999S")
    }
    
  },
  created(){
   
    if (sessionStorage.getItem('userData')) {
      const userData = JSON.parse(sessionStorage.getItem("userData"));
      this.cardNo = (userData.cretType == "1010" && userData.cretNo.length == 18) ? userData.cretNo : ''
    }
    if (sessionStorage.getItem('SFZvalue')) {
      this.cardNo = sessionStorage.getItem('SFZvalue')
       this.boxShow=false
      
    }
  }
};
</script>

<style lang="less" scoped>
.view-box {
  display: flex;
  // align-items: center;
  justify-content: center;
  padding-top: 50%;
}
.input-box {
  width: 80vw;
  height: 60px;
  // background-color: #002686;
  position: relative;
  text-align: left;
  border-radius: 4px;
  border: 1px solid var(--color);
  span {
    position: absolute;
    line-height: 60px;
    font-size: 18px;
    color: var(--color);
    font-weight: 500;
    left: 10px;
    z-index: 22;
    transition: all 0.5s;
    transform-origin: left top;
    box-sizing: border-box;
  }
  .span {
    transform: scale(0.6);
    opacity: 0.8;
  }
  input {
    outline: none;
    border: none;
    position: absolute;
    bottom: 5px;
    width: 100%;
    height: 20px;
    // line-height: 20px;
    background: none;
    color: #303133;
    padding-left: 10px;
    box-sizing: border-box;
  }
}
</style>
