<template>
  <div class="view-box">
    <div :class="['result-header',{'error-result':error}]">
      <van-icon :name="error ?'warning-o':'passed'"></van-icon>
      {{title}}
    </div>
    <div class="view" v-if="success">
      <div class="success-card">
        <div class="success-content content-worp" data-title="">{{result.order[0]}}</div>
        <div class="success-content" v-if="result.order&&result.order[1]">{{result.order[1]}}</div>
        <div class="success-content" v-if="result.order&&result.order[2]">{{result.order[2]}}</div>
        <div class="success-content" v-if="result.order&&result.order[3]">{{result.order[3]}}</div>
        <div class="success-content" v-if="result.order&&result.order[4]">{{result.order[4]}}</div>
        <div class="success-content" v-if="result.order&&result.order[5]">{{result.order[5]}}</div>
        <div class="success-content" v-if="result.order&&result.order[6]">{{result.order[6]}}</div>
        <div class="success-content" data-title="">{{tipstarttime}}</div>
        <div class="success-content" data-title="">{{result.name}}</div>
        <div class="success-content" data-title="">{{rcgnNm}}</div>
        <div class="go-policy" @click.stop="goDetail" v-if="$isWechat && !yntInfo"></div>
        <div class="go-policy" @click.stop="goYntApp" v-if="yntInfo"></div>
      </div>
      <div class="success-card prd">
        <div class="success-content" data-title="">{{result.prdname}}</div>
        <div class="success-content" data-title="">{{insurePeriod}}</div>
        <div
          class="success-content"
          v-for="(item,index) in liability"
          :data-title="index === 0?'':''"
          :key="item.modifyTime+index"
        >{{item.ilName+(item.description?`(${item.description})`:"")}}</div>
        <div
          class="show-more"
          @click="showAll = !showAll"
          v-if="result.prdInsuranceDutyList&&result.prdInsuranceDutyList.length > 2"
        >
          <img src=@/assets/svg/down.svg alt="" :class="{showmore:showAll}">
          {{showAll ? "":""}}
        </div>
      </div>
      <div class="success-card prd" v-if="this.result.cardSaleType === '2'">
        <div class="qrcode" ref="qrcode" data-tip=""></div>
      </div>
      <div class="success-card wechat">
        <div class="wechat-line">
          :
          <div class="follow" @click="doCopy">{{$isWechat?"":""}}</div>
        </div>
        <div class="wechat-des">
          <img src="~@/assets/image/jxrs.png" alt>
          <div></div>
        </div>
      </div>
    </div>
    <div class="view" v-if="error">
      <div class="success-card error">{{$route.params.message}}</div>
      <div class="go-ynt" @click.stop="goYntApp" v-if="yntInfo"></div>
    </div>
    <div class="view" v-if="pending">
      <div class="success-card pending">{{$route.params.message||""}}</div>
      <div class="success-card wechat">
        <div class="wechat-line">
          :
          <div class="follow" @click="doCopy">{{$isWechat?"":""}}</div>
        </div>
        <div class="wechat-des">
          <img src="~@/assets/image/jxrs.png" alt>
          <div></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import QRCode from "qrcodejs2";
import loading from "@/components/loading/index.js"

export default {
  data() {
    return {
      error: false,
      success: true,
      showAll: false,
      pending: false,
      yntInfo:{},
      cardcheckHold:{},
      insuredStatus:'',
      rcgnNm:'',
      getList:[],
      getCodeList:[]
    };
  },
  computed: {
    ...mapState({
      result: state => state.app.result ||{},
      holdData:state => state.app.holdData,
      channelCode: state => state.app.channelCode,
      productData: state => state.app.productData,
      orderCode:state => state.app.orderCode,
      batchNo:state => state.app.batchNo,
    }),
    title() {
           if (this.error) return "";
      if (this.pending) return "";
      return (this.result && this.result.msg) || ",";
    },
    liability() {
      const liability = this.result.prdInsuranceDutyList || [];
      return liability.slice(0, this.showAll ? 999 : 2);
    },
    insurePeriod() {
      if (this.result.plan) {
        let insurePeriod = "";
        const plan = this.result.plan;
        switch (plan.insuYearFlag) {
          case "Y":
            insurePeriod = plan.insuYear + "";
            break;
          case "M":
            insurePeriod = plan.insuYear + "";
            break;
          case "D":
            insurePeriod = plan.insuYear + "";
            break;
          case "A":
            insurePeriod =
              "" + plan.insuYear + "";
            break;
          default:
            insurePeriod = "";
            break;
        }
        return insurePeriod;
      } else {
        return "";
      }
    },
    tipstarttime(){
      const date = JSON.parse(localStorage.getItem('card-holdResult')).tipstarttime || "";
      const takeEffectFlag=JSON.parse(localStorage.getItem('card-holdResult')).takeEffectFlag;
      if(takeEffectFlag==1){
        const tipstarttime = new Date(+new Date(date));
        let y = tipstarttime.getFullYear(), m = tipstarttime.getMonth() + 1, d = tipstarttime.getDate(), h= tipstarttime.getHours();
        return y + '-' + (m > 9 ? m : `0${m}`) + '-' + (d > 9 ? d : `0${d}`)+" "+(h > 9 ? h : `0${h}`)+":00";
      }else{
        const tipstarttime = new Date(+new Date(date) + 86400000);
        let y = tipstarttime.getFullYear(), m = tipstarttime.getMonth() + 1, d = tipstarttime.getDate();
        return y + '-' + (m > 9 ? m : `0${m}`) + '-' + (d > 9 ? d : `0${d}`)+" "+"0";
      } 
    },
    endTime(){
      let endTime = JSON.parse(localStorage.getItem('card-holdResult')).tipendtime || "";
      const takeEffectFlag=JSON.parse(localStorage.getItem('card-holdResult')).takeEffectFlag;
      const date = new Date(this.tipstarttime) ;
      let y = date.getFullYear(), m = date.getMonth() + 1, d = date.getDate(), h=date.getHours();

      const insuYear = JSON.parse(localStorage.getItem('card-holdResult')).plan.insuYear || "";
      const insuYearFlag = JSON.parse(localStorage.getItem('card-holdResult')).plan.insuYearFlag || "";

      if(insuYearFlag == "Y"){
        y = y + parseInt(insuYear);
      }
      if(insuYearFlag == "M"){
        m = m + parseInt(insuYear) 
        if(m > 12){
          y = y + 1
          m = m - 12
        }
      }
      if(insuYearFlag == "D"){
        let newDayDate = new Date(+new Date(y + '/' + m + '/' + d) + 86400000 * parseInt(insuYear))
        d = newDayDate.getDate();
        y = newDayDate.getFullYear();
        m = newDayDate.getMonth()+1;
      }
        
      // ==  + 
      if(takeEffectFlag==1){
        return y + '-' + (m > 9 ? m : `0${m}`) + '-' + (d > 9 ? d : `0${d}`)+" "+(h > 9 ? h : `0${h}`)+":00";
      }else{
        let newdate = new Date(+new Date(y + '/' + m + '/' + d) - 86400000);
        d = newdate.getDate();
        y = newdate.getFullYear();
        m = newdate.getMonth() + 1;
        return y + '-' + (m > 9 ? m : `0${m}`) + '-' + (d > 9 ? d : `0${d}`);
      }
    }
  },
  created() {
    console.log(this.te);
    this.cardcheckHold = JSON.parse(localStorage.getItem('card-checkHold')) || "";
    if(this.cardcheckHold){
      this.rcgnNm = JSON.parse(localStorage.getItem('card-checkHold')).rcgnNm
    }
    this.yntInfo = JSON.parse(sessionStorage.getItem('yntInfo'))
    if (this.$route.params.type && this.$route.params.type === "0") {
      this.error = true;
      this.success = false;
      this.pending = false;
      if(this.orderCode){
        this.passBackInfo();
      }
    } else if (this.$route.params.type && this.$route.params.type === "2") {
      this.error = false;
      this.success = false;
      this.pending = true;
      if(this.orderCode){
        this.passBackInfo();
      }
    }
    console.log(this.result,'result')
    if(this.$route.params.type && this.$route.params.type === "1"){ //  3  1
      this.passBackInfo();
      const event = TINGYUN.createEvent({
        name: "",
        key: "3ef31450-3044-4973-bc8c-7007278ea9e4"
      })
      event.end();
    }
    this.traceBack()
  },
  mounted() {
    if (this.$route.params.type && this.$route.params.type === "0") return;
    if (this.result.cardSaleType === "2") {
      this.$refs.qrcode.innerHTML = "";
      new QRCode(this.$refs.qrcode, {
        text: `${location.origin +
          location.pathname.replace(
            "/elife-card",
            this.$pathname
          )}#/card-activation-select?pid=${this.result.prd_id}&name=${
          this.result.name
        }&cdn=${encodeURIComponent(this.result.priCardNo)}&btn=${this.result.batchNo}&cds=${this.result.cardno}`,
        width: innerWidth * 0.25,
        height: innerWidth * 0.25,
        colorDark: "#333333", //
        colorLight: "#ffffff", //
        correctLevel: QRCode.CorrectLevel.L //L/M/H
      });
    }
    this.getList = JSON.parse(localStorage.getItem("listKeyArray"))
      this.getList.forEach( i => {
        this.getCodeList.push(i.keyLinkCode)
    })
    if(window._yfx_sendorderid){
      this.shutDown()
    }else{
      setTimeout(() => {
        this.shutDown()
      }, 5000);
      console.log("")
    }
  },
  methods: {
    goDetail() {
      window.location.href = `${location.origin +
        location.pathname.replace(
          "/elife-card",
          this.$pathname
        )}#/user/new-policy/${this.channelCode}`;
    },
    goYntApp() {
      // app
      //  https://ram.ccb.today/areo/app/#/rris/response?custType=xx&yntCode=xx&insuredStatus=xx&remark=xx&scene=xx
      // :  https://rris.ccb.com/areo/app/#/rris/response?custType=xx&yntCode=xx&insuredStatus=xx&remark=xx&scene=xx
      let type = (this.$route.params.type == 3 || this.$route.params.type == 1) ? '1' : '0'
      if(location.host == 'elife.ccb-life.com.cn'){
        window.location.href = `https://rris.ccb.com/areo/app/#/rris/20220722_CCB_LIFE/response?custType=${this.yntInfo.custType}&yntCode=${this.yntInfo.yntCode}&insuredStatus=${type}&remark=${this.$route.params.type==3?'':this.$route.params.message}&scene=${this.yntInfo.scene}&promoterCode=${this.yntInfo.promoterCode}`
      }else{
        window.location.href = `https://ram.ccb.today/areo/app/#/rris/20220722_CCB_LIFE/response?custType=${this.yntInfo.custType}&yntCode=${this.yntInfo.yntCode}&insuredStatus=${type}&remark=${this.$route.params.type==3?'':this.$route.params.message}&scene=${this.yntInfo.scene}&promoterCode=${this.yntInfo.promoterCode}`
      }
    },
    doCopy() {
      console.log(this.$isWechat);

      this.$copyText("ccblife_official")
        .then(() => {
          console.log("success");
        })
        .catch(() => {
          console.log("failed");
        });
      if (this.$isWechat) {
        window.location.href =
          "https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MjM5MjA2NzQ5NA==&scene=124#wechat_redirect";
      }
    },
    passBackInfo(){
      console.log("this.productDat",this.productData)
      if(this.error){
        this.insuredStatus=0
      }else if(this.success || this.pending){
        this.insuredStatus=1
      }
      var  policyNo='';
      var  remark='';
      if(this.result.order){
        const length=this.result.order.length
        this.result.order.forEach((item,index)=>{
          if(index+1 < length){
            policyNo=policyNo + item +","
          }else{
            policyNo=policyNo + item;
          }
        });
        remark=remark +'success'
      }else{
        remark=this.$route.params.message;
      }
      const userMessage=JSON.parse(sessionStorage.getItem('userMessage'))
      if(userMessage){
        const policyMessage={
          recogNizee:userMessage.recogNizee,
          trainClassid:userMessage.trainClassid,
          insuranceid:userMessage.insuranceid,
          ccbempid:userMessage.ccbempid,
          trainClassStaDate:userMessage.trainClassStaDate,
          trainClassEndDate:userMessage.trainClassEndDate,
          cvaildate:userMessage.cvaildate,
          cid:userMessage.cid,
          bus:userMessage.bus,
          orderNo:this.orderCode,
          policyNo:policyNo,
          insuredStatus:this.insuredStatus,
          trainClassName:userMessage.trainClassName,
          campusName:userMessage.campusName,
          remark:remark
        }
        this.$api.card.passBackInfo(policyMessage).then(res =>{
          if(res.status=="1"){
            loading.hide();
          }
        })
      }
    },
    traceBack(){
      let TYPE = JSON.parse(localStorage.getItem('TYPE'))
      let SET_IMG_CODE = JSON.parse(localStorage.getItem('SET_IMG_CODE'))
      let PRODUCTDATA = JSON.parse(localStorage.getItem('PRODUCTDATA'))
      window._yfx_website = TYPE || "10000004"; //
      //
      window._yfx_isopenimg = "auto"; 
      window._yfx_setimgsession = SET_IMG_CODE.traceId;
      window._yfx_imgversion = SET_IMG_CODE.verNo; //
      window._yfx_imggoodsid = SET_IMG_CODE.prodId; //id
      window._yfx_imggoodsname = PRODUCTDATA.prd_name; //
      window._yfx_imgpagenum = "1"; //11
      window._yfx_imgpagename = ""; //

      //falsetrue
      window._yfx_isdebug = false;
      let scriptDom = document.createElement("script");
      scriptDom.charset = "utf-8";
      scriptDom.src =
        location.origin +
        location.pathname.replace("/elife-card", "") +
        JSON.parse(localStorage.getItem('url')) || 'statics/js/etbsimg.js';
      let scriptDoms = document.getElementsByTagName("script")[0];
      scriptDoms.parentNode.insertBefore(scriptDom, scriptDoms);
      console.log("scriptDomscriptDom")
    },

    shutDown(){
      var BATCHNO = JSON.parse(localStorage.getItem("BATCHNO"))  //
      var ORDERCODE = JSON.parse(localStorage.getItem("ORDERCODE"))  //
      window._yfx_sendorderid &&
      window._yfx_sendorderid(
        BATCHNO + (this.orderCode ?? ORDERCODE ),
        this.orderCode ?? ORDERCODE,
      );
      // +
      // this.$state.orderCode  
      // this.$store.state.batchNo  
      if(this.getCodeList.includes('A15')){
        this.addLinkDetail('A06')
        console.log('A15--')
      }
    },
    // 
    addLinkDetail(code){
      let ObjectList =  this.getList.filter( i => i.keyLinkCode === code)
      let getTraceId = JSON.parse(localStorage.getItem('SET_IMG_CODE'))
      let data = {
        imgSession :this.imgCode.traceId ? this.imgCode.traceId : getTraceId.traceId,  //id
        keyLinkName:ObjectList[0].keyLinkName, //
        keyLinkCode:ObjectList[0].keyLinkCode // code
      }
      console.log(data,"data--1157")
      window._yfx_addKeyLinkDetail(data)
    },
  }
};
</script>

<style lang="less" scoped>
@font10: 2.7vw;
@font12: 3.2vw;
@font14: 3.7vw;
@font16: 4.2vw;
@font18: 4.8vw;
.view-box {
  height: 100vh;
  width: 100vw;
  background-color: #fff;
  .result-header {
    background-color: #4ca2fe;
    width: 100%;
    height: 30vw;
    padding: 5.3vw 5%;
    text-align: left;
    font-size: 4.2vw;
    font-weight: 500;
    color: #fff;
    &.error-result {
      background-color: #ffb35b;
    }
    /deep/ .van-icon {
      &::before {
        vertical-align: middle;
      }
    }
  }
  .view {
    height: 100vh;
    width: 100vw;
    background-color: transparent;
    position: absolute;
    top: 0;
    padding: 17vw 5vw 0;
    .success-card {
      width: 90vw;
      border-radius: 5px;
      padding-top: 4vw;
      box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
      position: relative;
      background-color: #fff;
      text-align: left;
      font-size: @font14;
      padding-bottom: 5vw;
      &.error,
      &.pending,
      &.wechat {
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 30px;
        min-height: 150px;
        text-align: center;
        padding: 15px;
        &::after {
          display: none;
        }
      }
      &::after {
        content: "";
        position: absolute;
        width: 100%;
        bottom: 19vw;
        border-bottom: 1px dashed #efefef;
      }
      &.wechat {
        align-items: flex-start;
        margin-top: 40px;
        flex-wrap: wrap;
        .wechat-line {
          width: 100%;
          flex-shrink: 0;
          font-weight: 500;
          color: #303133;
          font-size: @font14;
          display: flex;
          justify-content: space-between;
          line-height: 25px;
          .follow {
            border: 1px solid #4ca2fe;
            border-radius: 1.1vw;
            color: #4ca2fe;
            padding: 0px 8px;
            font-size: @font12;
          }
        }
        .wechat-des {
          width: 100%;
          height: 32vw;
          flex-shrink: 0;
          display: flex;
          justify-content: space-between;
          align-items: center;
          img {
            width: 23vw;
            height: 23vw;
            flex-shrink: 0;
          }
          div {
            width: calc(~"100% - 30vw");
            color: #979797;
            font-size: @font14;
            text-align: left;
            line-height: 1.5em;
          }
        }
      }
      &.prd {
        margin-top: 5vw;
        height: auto;
        padding-bottom: 15px;
        &::after {
          display: none;
          border: none;
        }
        .show-more {
          line-height: 15vw;
          text-align: center;
          color: #4ca2fe;
          img {
            height: 3.2vw;
            vertical-align: middle;
            margin-right: 5px;
            transition: all 1s;
            &.showmore {
              transform: rotateZ(-180deg);
            }
          }
        }
      }
      .success-content {
        position: relative;
        text-indent: 23vw;
        margin-top: 3vw;
        min-height: 5vw;
        &::before {
          content: attr(data-title);
          color: #979797;
          position: absolute;
          left: -18vw;
        }
      }
      .go-policy {
        width: 40vw;
        text-align: center;
        line-height: 9.6vw;
        background: linear-gradient(90deg, #33adfd 0%, #558df8 100%);
        border-radius: 1.1vw;
        color: #fff;
        margin: 10vw auto 0;
      }
      .qrcode {
        text-align: left;
        margin-bottom: 30px;
        text-indent: 20px;
        position: relative;
        height: 30vw;
        line-height: 30vw;
        &::before {
          content: attr(data-tip);
          font-size: 3.2vw;
          color: #303133;
        }
        /deep/ canvas,
        /deep/ img {
          // margin: 0 auto;
          // float: right;
          position: absolute;
          right: 20px;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
  }
  .go-ynt{
      width: 40vw;
      text-align: center;
      line-height: 9.6vw;
      background: linear-gradient(90deg, #33adfd 0%, #558df8 100%);
      border-radius: 1.1vw;
      color: #fff;
      margin: 10vw auto 0;
  }
}
</style>
