<template>
    <div>

    </div>
</template>
<script>
import skeleton from "@/components/skeleton";
import { mapState } from "vuex";
import md5 from "js-md5"
export default {
    data () {
        return{
            text:'',
            bankInfo:{}
        }
    },
    computed:{
        ...mapState({
            holdData:state => state.app.holdData,
            productData: state => state.app.productData,
            batchInfo: state => state.app.batchInfo,
            userInfo: state => state.app.policyInfo
        }),
    },
    methods:{
        callBack(){
            var requestObj  =  {
            action:'setCache',/**/
            params:{
                key: this.bankInfo.ysId, // keyremark2
                value: location.origin + location.pathname.replace("/elife-card", '') + "elife-card/#/card/card-result/3/0/" + this.holdData.orderCode // urlAPPvalueurl
                }
            }
            window.CCBBridge.requestNative(JSON.stringify(requestObj), 'callBacks'); 
        },
        payStatus(){
            console.log('')
        },
        MBC_PAYINFO(){
            var payInfo = {
                MERCHANTID:this.bankInfo.ccbMerchantId,	//	CHAR(15)	Y	T	
                POSID:this.bankInfo.ccbPosId,	//	CHAR(9)	Y	T	
                BRANCHID:this.bankInfo.ccbBranchId,	//	CHAR(9)	Y	T	
                POSID19:'',	//	19	CHAR(19)	N	F	MAC
                ORDERID:this.holdData.orderCode,	//		CHAR(30)	Y	T	30
                PAYMENT:this.batchInfo.initPyFAmt,	//		NUMBER(16,2)	Y	T	30
                CURCODE:'01',	//		CHAR(2)	Y	T	01
                TXCODE:'520100',	//		CHAR(6)	Y	T	520100
                REMARK1:'',	//	1	CHAR(30)	Y	T	
                REMARK2:this.bankInfo.ysId,	//	2	CHAR(30)	Y	T	YS
                TYPE:'1',	//		CHAR(1)	Y	T	1- 
                GATEWAY:'0',	//		CHAR(100)	Y	T	0
                CLIENTIP:'',	//	IP	CHAR(40)	N	T	IP
                REGINFO:escape(this.holdData.plchdNm),	//		CHAR(256)	N	T	escape
                PROINFO:escape(this.productData.prd_name),	//		CHAR(256)	N	T	escape
                REFERER:'',	//	URL	CHAR(100)	N	T	
                THIRDAPPINFO:'comccbpay1234567890cloudmerchant',	//		CHAR(40)	Y	T	APPcomccbpay1234567890cloudmerchant
                TIMEOUT:'',	//		CHAR(14)	N	F	YYYYMMDDHHMMSS20120214143005
                // > TIMEOUT
                // MACMAC
                PAYBITMAP:'',	//		CHAR(10)	N	F	0100000000
                // MACMAC
                // PLATFORMPUB:'MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCgAj+EgYdKHMHBWkr/XHjq5je7BrpG7vnjEnf8SqsZApmfjWDnXQu/TtYwFF9Z5ExoAPeLZK6J2/jBON8gjlppnOrEzIirUIt4x5yHEGq4io+lGtYsvShJmzR8It8tbKzVIb1laanJs9Vi4CtJZAVKn9axaTt9v8TuiZZ5oAy2WQIDAQAB',	//		VARCHAR(256)	Y	F	MD5
                PLATFORMPUB:this.bankInfo.serPublicKey,	//		VARCHAR(256)	Y	F	MD5
                MAC:'',	//	MD5	CHAR(32)	Y	T	MD5MAC32
                PLATFORMID:this.bankInfo.ysId,	//		CHAR(16)	Y	T	MAC
                ENCPUB:this.bankInfo.encryptPublicKey,	//		VARCHAR(512)	Y	F	30RSAbase64
                // MAC
            }
            // MAC
            // MERCHANTID=123456789&POSID=000000000&BRANCHID=110000000&ORDERID=19991101234
            // &PAYMENT=0.01&CURCODE=01&TXCODE=520100&REMARK1=&REMARK2=&TYPE=1&GATEWAY=&CLIENTIP=172.0.0.1
            // &REGINFO=%u5C0F%u98DE%u4FA0&PROINFO=%u5145%u503C%u5361&REFERER=&THIRDAPPINFO=comccbpay1234567890cloudmerchant
            // &TIMEOUT=20161028101226&PAYBITMAP=0100000000&MAC=4f9033be946e7dcd78886fdb4b2b0ec3
            // &PLATFORMID=YS0000000000000001&ENCPUB=ZGdXNUo3MXA5allmY0dUQm1mRW...
            // '+payInfo.REMARK2+'
            let str ='MERCHANTID='+payInfo.MERCHANTID+'&POSID='+payInfo.POSID+'&BRANCHID='+payInfo.BRANCHID+'&ORDERID='+
                      payInfo.ORDERID+'&PAYMENT='+payInfo.PAYMENT+'&CURCODE='+payInfo.CURCODE+'&TXCODE='+payInfo.TXCODE+
                     '&REMARK1=&REMARK2='+payInfo.REMARK2+'&TYPE='+payInfo.TYPE+'&GATEWAY='+payInfo.GATEWAY+'&CLIENTIP=172.0.0.1'+
                     '&REGINFO='+payInfo.REGINFO+'&PROINFO='+payInfo.PROINFO+'&REFERER=&THIRDAPPINFO='+payInfo.THIRDAPPINFO+
                     '&PLATFORMPUB='+payInfo.PLATFORMPUB
            let strs = 'MERCHANTID='+payInfo.MERCHANTID+'&POSID='+payInfo.POSID+'&BRANCHID='+payInfo.BRANCHID+'&ORDERID='+
                       payInfo.ORDERID+'&PAYMENT='+payInfo.PAYMENT+'&CURCODE='+payInfo.CURCODE+'&TXCODE='+payInfo.TXCODE+
                      '&REMARK1=&REMARK2='+payInfo.REMARK2+'&TYPE='+payInfo.TYPE+'&GATEWAY='+payInfo.GATEWAY+'&CLIENTIP=172.0.0.1'+
                      '&REGINFO='+payInfo.REGINFO+'&PROINFO='+payInfo.PROINFO+'&REFERER=&THIRDAPPINFO='+payInfo.THIRDAPPINFO
            payInfo.MAC = md5(str)
            let data = strs + '&MAC='+payInfo.MAC+'&PLATFORMID='+payInfo.PLATFORMID+'&ENCPUB='+ payInfo.ENCPUB
            return data;
        },
        MBS_DIRECT_PAY(payInfo){
            skeleton.hide()
            window.mbspay.directpay(payInfo); 
        },
        MBS_DIRECT_PAYS(){
            skeleton.hide()
            window.location="mbspay://direct?"+ this.MBC_PAYINFO();
        },
        payCheck(payInfo){
            // 
            const ua = navigator.userAgent.toLowerCase();
            if (ua.indexOf("android") != -1) {
                /**
                 * 
                 * @param {String} payInfo payInfoSDK
                 */
                // 
                this.MBS_DIRECT_PAY(payInfo)
            } else if (ua.indexOf("iphone") != -1) {
                this.MBS_DIRECT_PAYS(payInfo)
            }
        }
    },
    mounted() {
        window.callBacks =  this.callBack()
        
    },
    created(){
        // eruda.init()
        this.bankInfo = this.$route.params.payData
        if (this.bankInfo) {
            sessionStorage.setItem('ccbPosid',this.bankInfo.ccbPosId)
            this.callBack()
            skeleton.show()
            this.payCheck(this.MBC_PAYINFO())
        }else{
            this.$router.go(-1)
        }
    }
}
</script>