<template>
  <div class="box">
    <van-nav-bar
      title=""
      right-text=""
    />
    <!-- B -->
    <!-- detail.data.prdName -->
    <div class="header">{{ prdName }}</div>
    <div class="line"></div>
    <div class="order-detail">
      <div
        v-for="(item, index) in orderDetail"
        :key="item.orderDes"
        class="detail_insurance"
      >
        <span class="left">{{ item.orderDes }}</span>
        <span :class="['right', { money: index == 2 ? true : false }]">{{
          item.detailDes
        }}</span>
      </div>
    </div>
    <div class="insurance-detail">
      <div
        v-for="item in insuranceDetail"
        :key="item.insuranceDes"
        class="detail_insurance"
      >
        <span class="left">{{ item.insuranceDes }}</span>
        <span class="right">{{ item.detailDes }}</span>
      </div>
    </div>
    <van-button
      type="primary"
      color="#4ca2fe"
      block
      class="payButton"
      @click="topay"
      v-if="paymentWait"
      ></van-button
    >
  </div>
</template>

<script>
import { Divider, Toast } from "vant";
import Vue from "vue";
import { NavBar } from "vant";
import loading from "@/components/loading/index.js";
import skeleton from "@/components/skeleton";
Vue.use(NavBar);
import { mapState, mapActions, mapGetters } from "vuex";
// import { Toast } from "vant";
export default {
  name: "view-detail",
  data() {
    return {
      selectOrderListRes:'',
      beOverDue: false,
      params: "", //
      prdName: "B",
      payOrder: "", //
      detail: "", //
      loginDetail: "", //
      insurerCerId: "", //
      paymentWait: true, //.payStatus				Short	[0-1-;2-;3-;4-]
      decrypt: "", //
      orderDetail: [
        {
          orderDes: ":",
          detailDes: "2022-01-18 16:01:22",
        },
        {
          orderDes: ":",
          detailDes: "12345234567892345678",
        },
        {
          orderDes: ":",
          detailDes: "1234",
        },
      ],
      insuranceDetail: [
        {
          insuranceDes: ":",
          detailDes: "",
        },
        {
          insuranceDes: ":",
          detailDes: "",
        },
        {
          insuranceDes: ":",
          detailDes: "411526200003196729",
        },
        {
          insuranceDes: ":",
          detailDes: "1980-01-01",
        },
      ],
    };
  },
  computed: {},
  methods: {
    topay() {
         const ua = navigator.userAgent.toLowerCase();
         let platForm;
      if (ua.indexOf("android") != -1) {
        platForm = "android";
      } else if (ua.indexOf("iphone") != -1) {
        platForm = "iPhone";
      } else {
        platForm = "pc";
      }
      let payData = {
        backUrlType:
          "elife-card/#/card/card-result/3/0/" + this.detail.data.orderCode, // url
        branchCode: this.detail.data.recentRecord.branchCode, //
        payTypeCode: this.selectOrderListRes.data.payTypeCode, //code 00  01: h5
        cardSaleType: this.selectOrderListRes.data.cardSaleType, // 
        ccbMerchantId: this.selectOrderListRes.data.merchantId, //  
        ccbPosId: this.selectOrderListRes.data.posId, // 
        opendId: localStorage.getItem("elife-openid") || "blank",
        wxCode: sessionStorage.getItem("elife-wxCode") || "",
        customerId: this.loginDetail.data.cusId || "",
        orderCode: this.detail.data.orderCode, //
        payAmount: this.detail.data.recentRecord.payAmount, // 
        platForm, //
        browserType:
          this.detail.data.policyList[0].channelCode === "0011" ||
          this.detail.data.policyList[0].channelCode === "9999"
            ? "01"
            : "02", //holdDataparams
        channelCode: this.detail.data.policyList[0].channelCode, //
        timestamp: new Date().getTime() + Math.random(),
        paySign: "", //
        userId: this.decrypt.data.USERID,
        initSumAmt: this.selectOrderListRes.data.initSumAmt, //
        initPyFAmt: this.selectOrderListRes.data.initPyFAmt, //
        coPyFAmt: this.selectOrderListRes.data.coPyFAmt, //
        discountAmtDesc: this.selectOrderListRes.data.discountAmtDesc || "", //
      };
      // 
      var payReqData = {
        branchCode: this.detail.data.recentRecord.branchCode, //
        payAmount: this.detail.data.recentRecord.payAmount, // 
        orderCode: this.detail.data.orderCode,
        // orderCode:'20221018000005247618'
      };
      // 
      this.api.card.checkNewPay(payReqData).then((paywayres) => {
        payData.paySign = paywayres.data.paySign;
        payData.trackvideo = paywayres.data.payNewSign || "";
        if (paywayres.status == "1") {
          // 
           this.api.card.mebOrderPush(payData, true).then((res) => {
          if (res.status == "1") {
          router.replace({
            name: "blank-page",
            params: {
              payData:res.data,
            },
          });
        } else {
          router.replace({
            name: "card-result",
            params: {
              type: "0",
              message:res.error.message,
            },
          });
        }
      });
        }else if(paywayres.status == "0"){
            skeleton.hide();
           loading.hide();
           this.$router.replace({
          name: "card-result",
          params: {
          type: "0",
          // message: this.selectOrderListRes.error.message
          message: paywayres.error.message
        }
      });

        }
      });
      console.log(payData, "payData");
    }
  },
  async created() {
    skeleton.show();
    let orderCode = this.$route.query.orderNo;
    let ccbParamSJ = this.$route.query.ccbParamSJ;
    // 
       // 
   this.selectOrderListRes=await this.api.card.selectOrderList({
          //  orderCode:'20221018000005247618'
           orderCode:orderCode
           }, true)
    if(this.selectOrderListRes.status=='1'){
      this.$store.commit("CONTINUE_PAY_MES", this.selectOrderListRes.data);
      // 
      this.decrypt= await this.api.card.encrypt({ cipherText: ccbParamSJ,ccbPosId:this.selectOrderListRes.data.posId });
       if(this.decrypt.status=='1'){
      // 
     this.loginDetail = await this.api.card.ccbLifeLogin({
         phoneNo:this.decrypt.data.mobile
        //  phoneNo:'15353535353'
         });
         if(this.loginDetail.status=='1'){
            sessionStorage.setItem(
          "elife-userData",
          JSON.stringify(this.loginDetail.data) 
        );
      const channelCode = sessionStorage.getItem("channelCodeOld");
     let payOnOrderList  = await this.api.card.payOnOrderList ({
      orderCode:orderCode,
      queryTimeArea: "0",
      channelCode: channelCode,
    });
    if(payOnOrderList.status=='1'){
 this.detail = await this.api.card.orderDetail({
      orderCode: orderCode,
      channelCode: channelCode,
    });
       if (this.detail.status == "1") {
      switch (this.detail.data.orderCretType) {
        case "1010":
          this.insurerCerId = "";
          break;
        case "1011":
          this.insurerCerId = "";
          break;
        case "1021":
          this.insurerCerId = "";
          break;
        case "1031":
          this.insurerCerId = "";
          break;
        case "1040":
          this.insurerCerId = "";
          break;
        case "1050":
          this.insurerCerId = "";
          break;
        case "1051":
          this.insurerCerId = "";
          break;
        case "1060":
          this.insurerCerId = "";
          break;
        case "1140":
          this.insurerCerId = "";
          break;
        case "1150":
          this.insurerCerId = "";
          break;
        case "1160":
          this.insurerCerId = "";
          break;
        case "1730":
          this.insurerCerId = "";
          break;
        case "1700":
          this.insurerCerId = "";
          break;
        case "1740":
          this.insurerCerId = "";
          break;
        default:
          this.insurerCerId = "";
          break;
      }
      this.prdName = this.detail.data.prdName;
      this.orderDetail[0].detailDes = this.detail.data.orderSubmitTime;
      this.orderDetail[1].detailDes = this.detail.data.orderCode;
      this.orderDetail[2].detailDes =
      this.selectOrderListRes.data.initSumAmt + "";
      this.insuranceDetail[0].detailDes = this.detail.data.insurerName;
      this.insuranceDetail[1].detailDes =
      this.detail.data.insurerSex == 1 ? "" : "";
      this.insuranceDetail[2].detailDes =
      this.insurerCerId + "\n" + this.detail.data.insurerCerId;
      this.insuranceDetail[3].detailDes = this.detail.data.birthday;
      let patState = this.detail.data.recentRecord.payStatus;
      skeleton.hide();
      loading.hide();
      // 24
      let datatime = this.detail.data.orderSubmitTime;
      let timeHandle = datatime.split("-").join("").split(":").join("");
      let ordertime = parseInt(timeHandle.replace(/\s+/g, ""));
      console.log(ordertime, "newtime2");
      let nowTime = new Date();
      let y = nowTime.getFullYear();
      let m = nowTime.getMonth() + 1;
      let d = nowTime.getDate();
      let h = nowTime.getHours();
      let f = nowTime.getMinutes();
      let ss = nowTime.getSeconds();
      let s = 0;
      ss < 10 ? (s = "0" + nowTime.getSeconds()) : (s = nowTime.getSeconds());
      let nowTimers = parseInt(y + "" + m + d + h + f + s);
      console.log(nowTimers, ordertime);
      console.log(nowTimers - ordertime, "");
      if (nowTimers - ordertime > 1000000) {
        this.beOverDue = true;
        console.log("24");
      }
      // 
      patState == 0 || patState == 1 || patState ===3
        ? (this.paymentWait = true)
        : (this.paymentWait = false);
      this.beOverDue ?this.paymentWait = false:this.paymentWait = true
        
    }else{
      skeleton.hide();
      loading.hide();
       this.$router.replace({
        name: "card-result",
        params: {
          type: "0",
          // message: this.selectOrderListRes.error.message
          message: this.detail.error.message
        }
      });
    }
    }
    }else{
         skeleton.hide();
      loading.hide();
       this.$router.replace({
        name: "card-result",
        params: {
          type: "0",
          // message: this.selectOrderListRes.error.message
          message: this.loginDetail.message
        }
      });
    }
         
        }
    }else if(this.selectOrderListRes.status=='0'){
      skeleton.hide();
      loading.hide();
       this.$router.replace({
        name: "card-result",
        params: {
          type: "0",
          message: this.selectOrderListRes.error.message
          // message: ''
        }
      });
    }
   
  },
};
</script>

<style lang="less" scoped>
@marginOfForm: 35.5vw;
@cardHeight: 12.5vw;
@gray: #efefef;
@font10: 2.7vw;
@font12: 3.2vw;
@font14: 3.7vw;
@font16: 4.2vw;
@font18: 4.8vw;
.box {
  width: 100%;
  height: 100vh;
  background-color: #e7e3e7;
  position: relative;
  .van-nav-bar {
    height: 7vh;
    line-height: 7vh;
    /deep/ .van-nav-bar__title {
      font-size: @font18;
    }
    /deep/ .van-nav-bar__text {
      font-size: @font18;
      color: #303133;
    }
  }
  .header {
    font-size: @font18;
    height: 6vh;
    line-height: 7vh;
    text-align: left;
    font-weight: 550;
    padding-left: 20px;
    background-color: #fffeff;
    margin-top: 10px;
  }
  .line {
    height: 1px;
    background-color: #e7e3e7;
    width: 92vw;
    margin: 0px 20px;
  }
  .order-detail {
    width: 100%;
    background-color: #fffeff !important;
  }
  .detail_insurance {
    width: 100%;
    font-size: @font16;
    line-height: 2.7em;
    color: #303133;
    height: 2.7em;
    .left {
      content: attr(data-des);
      float: left;
      margin-left: 20px;
      width: 23vw;
      text-align: left;
    }
    .right {
      content: attr(data-detail);
      float: left;
    }
    .money {
      color: #ebae63;
    }
  }
  .order-detail {
    width: 100%;
    background-color: rgb(187, 173, 176);
  }

  .insurance-detail {
    width: 100%;
    background-color: #fffeff;
    margin-top: 14px;
  }
  .payButton {
    position: absolute;
    bottom: 30px;
    width: 94%;
    height: 40px;
    margin-left: 3%;
    border-radius: 20px;
  }
}
</style>

