<template>
  <div>
    <van-popup
      v-model="showPopup"
      round
      closeable
      position="bottom"
      :style="{ height: '70%' }"
    >
      <div class="cont">
        <div class="cont_title"></div>

        <div
          class="info_personnel_all"
          v-for="(item, index) of dataList"
          :key="index"
        >
          <div class="ticket_buyer_info_personnel_right">
            <van-checkbox
              v-model="item.checked"
              @change="handleCheck(index, $event)"
            >
            </van-checkbox>
          </div>
          <div class="ticket_buyer_info_personnel">
            <div class="ticket_buyer_info_personnel_left">
              <div class="ticket_buyer_info_personnel_left_name">
                <div class="personnel_left_name_div1">
                  <div class="personnel_left_name_div1_name">
                    {{ item.name }}
                  </div>
                  <div class="personnel_left_name_div1_phone">
                    {{ item.phone }}
                  </div>
                </div>
              </div>

              <div class="ticket_buyer_info_personnel_left_name">
                <div class="personnel_left_name_div1">
                  <div class="personnel_left_name_div1_name">
                    
                  </div>
                  <div class="personnel_left_name_div1_phone">
                    {{ item.idCard }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="bottom_button">
          <van-button
            block
            color="#4ca2fe"
            class="next"
            @click.stop="bottomNext"
            > 
          </van-button>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showPopup: false,
      selectedIndex: -1, // 
      dataList: [
        {
          checked: false,
          name: "1",
          idCard: "110101200207080858",
          phone: "138****3458",
        },
        {
          checked: false,
          name: "2",
          idCard: "110101200207080858",
          phone: "138****3458",
        },
        {
          checked: false,
          name: "*",
          idCard: "110101200207080858",
          phone: "138****3458",
        },
        {
          checked: false,
          name: "*",
          id: "110101200207080858",
          phone: "138****3458",
        },
        // ...
      ],
    };
  },
  methods: {
    handleCheck(index, checked) {
      // 
      if (checked) {
        this.dataList.forEach((item, idx) => {
          if (idx !== index) {
            item.checked = false;
          }
        });
        this.selectedIndex = index;
      } else {
        //  selectedIndex
        if (this.selectedIndex === index) {
          this.selectedIndex = -1;
        }
      }
    },
    bottomNext() {
      if (this.selectedIndex === -1) {
        this.$toast("");
      } else {
        // 
        const selectedPerson = this.dataList[this.selectedIndex];
        console.log("", selectedPerson);
        this.$emit("updateSelectedPerson", selectedPerson); //  selectedPerson
        // 
        this.showPopup = false;
      }
    },
  },
};
</script>
<style lang="less" scoped>
.cont {
  display: flex;
  flex-direction: column;
  padding: 5vw;
  margin-bottom: 10vw;
}

.cont_title {
  font-size: 5vw;
  color: #2c2e30;
}

.info_personnel_all {
  display: flex;
  width: 100%;
}

.ticket_buyer_info_personnel {
  margin-top: 2vw;
  padding: 3vw;
  background: #f6f8fb;
  border-radius: 7px;
  display: flex;
  width: 100%;
  margin-left: 3vw;
}

.ticket_buyer_info_personnel_left {
  display: flex;
  flex-direction: column;
  width: 100%;

  .ticket_buyer_info_personnel_left_name {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.personnel_left_name_div1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1vw;
  font-size: 4vw;
}

.personnel_left_name_div1_name {
  font-size: 4vw;
}

.personnel_left_name_div1_phone {
  font-size: 3.7vw;
  margin-left: 1vw;
}

.ticket_buyer_info_personnel_right {
  display: flex;
  justify-content: center;
  align-items: center;
}

.bottom_button {
  width: 90%;
  position: fixed;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  background-color: #fff;
  margin: 10px auto 0;
  .next {
    border-radius: 56px;

    height: 11vw;
    line-height: 11vw;
    font-size: 4.8vw;
  }
}
</style>
