<template>
  <div class="view-box">
    <div class="image">
      <van-image :src="src" width="100vw" :class="{'loading-image':true}">
        <van-loading slot="loading" type="spinner" size="40" vertical></van-loading>
      </van-image>
    </div>
    <slot name="pop"></slot>
    <div class="bottom">
      <van-button block color="#4ca2fe" class="next" @click.stop="next">{{buttonName || ""}}</van-button>
    </div>
    <br/>
    <div style="display: flex;align-items: center;justify-content: center;color: #acacac;">
        <div style="font-size: 14px;">&nbsp;</div>
        <div style="border: 1px solid #acacac;border-radius:3px;font-size: 12px;height: 14px;line-height: 12px;">IPv6</div>
        <div style="font-size: 14px;">&nbsp;</div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
const baseUrl = location.origin + location.pathname.replace("/elife-card", '')
const isPrd = process.env.NODE_ENV === "production";

export default {
  data() {
    return {
      // src: require("../../../assets/image/2.jpg")
    };
  },
  computed:{
    ...mapState({
     src :state => isPrd ? baseUrl+state.app.collection.src:require("../../../assets/image/qinwuyuan.jpg"),
     buttonName:state => state.app.collection.buttons && state.app.collection.buttons.layout
   })
  },
  methods:{
    next() {
      this.$emit("next")
    },

  }
};
</script>

<style lang="less" scoped>
.view-box {
  .image {
    height: calc(~"93.5vh - 4vw");
    overflow-y: auto;
  }
  .bottom {
    width: 96vw;
    position: absolute;
    bottom: 2vw;
    transform: translateX(2vw);
    .next {
      height: 11vw;
      line-height: 11vw;
      font-size: 4.8vw;
    }
  }
}
</style>

