<template>
  <van-popup
    v-model="show"
    position="bottom"
    :style="{'max-height':'75%'}"
    safe-area-inset-bottom
    closeable
  >
    <div class="logo">
      <h4></h4>
    </div>
    <div class="pop-content">
      <div class="rela">
        <div class="rela-title"></div>
        <div class="rela-content">
          <span :class="{active:form.rela.value===1}" @click="showID"></span>
          <span v-if="!who" :class="{active:form.rela.value===2}" @click="showId"></span>
        </div>
      </div>
      <e-input
        :maxlength="20"
        title=""
        :error="cardNoError"
        @input="inputCardNo"
        v-model="form.idCard.value"
        mask="999999 99999999 999S"
        @blur="blurCardInput"
      >
        <span style="font-size:3.73vw"></span>
      </e-input>
      <e-input
        v-if="CCBschlool"
        :maxlength="30"
        v-model="form.grade.value"
        placeholder=""
      >
        <span style="font-size:3.73vw">{{schoolName}}</span>
      </e-input>
      <span v-if="CCBschlool" class="hint">{{schoolHint}}</span>
      
      <div class="columns" v-for="a in area" :key="a.key">
        <div class="select" v-if="a.type==='select'">
          <div class="rela-title">{{a.name}}</div>
          <div @click="popArea(a)" :class="form[a.key].value ? '':'active'">{{form[a.key].value ? form[a.key].value :''}}</div>
        </div>
      </div>
      <van-popup v-model="isOptions" position="bottom" :overlay="true">
        <van-picker show-toolbar  @confirm="confirm" @cancel="cancel"  :columns="optionList" value-key="value"></van-picker>
      </van-popup>
      <!-- <popup-picker v-model="form.plan.value" :columns="columns[0].columns" show-toolbar></popup-picker> -->
      <div class="columns" v-for="v in columns" :key="v.key">
        <div class="plans-box" :data-title="v.name" v-if="v.type==='block'">
          <div class="plans">
            <div
              :class="['item',{active:form[v.key].value === item.key}]"
              v-for="item in v.columns"
              :key="item.key"
              @click="form[v.key].value = item.key"
            >{{item.name}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <van-button
        block
        color="#4ca2fe"
        class="next"
        @click.stop="next"
        :disabled="disabled"
      >{{buttonName || ""}}</van-button>
    </div>
  </van-popup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import eInput from "@/components/input";
// import popupPicker from "@/components/popup-picker";
import util, { setMap, filterMap } from "@/components/_utils";

export default {
  name: "slot-popup",
  components: {
    eInput,
    // popupPicker
  },
  props: {
    value: { type: Boolean, default: false }
  },
  data() {
    return {
      cretType:'',
      userId:'',
      show: false,
      showPicker: false,
      disabled: true,
      plan: "",
      rela: undefined,
      cardNoError:false,
      isOptions:false,
      optionList:[],
      key:'',
      form: {
        rela: {
          value: "",
          check: {
            required: true
          }
        },
        grade: {
          value: "",
          check: {
            required: true
          }
        },
        idCard: {
          value: "",
          maps: [
            { sex: util.getSexOld },
            { age: util.getAge },
            { birthday: util.getBirthday }
          ],
          check: {
            required: true,
            func: util.checkIdCard
          }
        },
      }
    };
  },
  watch: {
    show(val) {
      this.$emit("input", val);
    },
    value(val) {
      this.show = val;
    },
    form: {
      handler() {
        this.disabled = !!this.checkForm();
      },
      deep: true
    }
  },
  computed: {
    ...mapState({
      userCode:state => state.app.userCode,
      buttonName: state => state.app.collection.buttons && state.app.collection.buttons.popup,
      area:state =>state.app.collection.option ? state.app.collection.option :[
              {
                name: "",
                type: "default",
                key: "default",
                map: {
                  value: "",
                  maps: [
                    {
                      planCode: "value"
                    }
                  ],
                  check: {
                    required: true
                  }
                },
                columns: state.app.collection.columns
              }
            ],
      columns: state =>
        state.app.collection.notColumns
          ? state.app.collection.columns
          : [
              {
                name: "",
                type: "blocks",
                key: "plan",
                map: {
                  value: "",
                  maps: [
                    {
                      planCode: "value"
                    }
                  ],
                  check: {
                    required: true
                  }
                },
                columns: state.app.collection.columns
              }
            ],
      endTip: state => state.app.collection.endTip,
      noMapTip: state => state.app.collection.noMapTip,
      nextFlag: state => state.app.collection.nextFlag,
      CCBschlool:state => state.app.collection.CCBschlool,
      who:state => state.app.collection.who,//
      schoolHint:state => state.app.collection.schoolHint,//
      schoolName:state => state.app.collection.schoolName,//
      sceneCode:state => state.app.collection.sceneCode,
      later: state => state.app.collection.later || 1,
      effectiveDate: state => state.app.collection.effectiveDate,
      share:state => state.app.collection.share
    })
  },
  methods: {
    ...mapActions(["shareOnWechat"]),
    confirm(e){
      this.form[this.key].value = e.value
      this.isOptions = false
    },
    cancel(){
      this.isOptions = false
    },
    popArea(active){
      this.key = active.key
      this.isOptions = true;
      this.optionList = active.map.list
    },
    showID(){
      this.form.rela.value=1;
      if(this.cretType == 'A' || this.cretType == '1010'){
        this.form.idCard.value=this.userId
      }
    },
    showId(){
      this.form.rela.value=2;
      this.form.idCard.value=''
    },
    getUserInfo(){
      console.log(this.userCode);
      let url = window.location.href;
      // console.log(url);
      // ?accessLink=c89fa794e90d4e759af8fe5aa8e3c532
      // let url = 'http://10.100.114.188:1080/elife-card/#/card/activity-collection/KDPC005?accessLink=c89fa794e90d4e759af8fe5aa8e3c532afafgsddg';
      let codeLink =url.split('?')[1];
      console.log(codeLink);
      if(codeLink){
        let user0 = codeLink.split('=')[0];
        let userCode = codeLink.split('=')[1];
        // let userCode0 = codeLink.split('=')[1];
        // let userCode = userCode0.substring(0,32);
        console.log(user0);
        console.log(userCode);
        if(user0 == 'accessLink'){
          sessionStorage.setItem("channelCode",'0037')
          if(userCode) {
            this.$store.commit('getCode', userCode)
            console.log(this.userCode);
            let param = {
              userCode: this.userCode,
              code: "qwychannel",
            }
            this.$api.common.userInfoApply(param, true).then(res => {
              console.log(res);
              this.userId = res.data.cretNo;
              this.cretType = res.data.cretType;
            })
          }
        }
      }
    },
    inputCardNo(cardNo) {
      console.log(cardNo);
      if(cardNo === '') return (this.cardNoError = false);
      this.cardNoError = !util.checkIdCard(cardNo)
    },
    eventClick(e) {
      console.log(e);
    },
    blurCardInput() {
      window.scroll(0, 0);
      document.body.scrollTop = document.body.scrollHeight;
    },
    goCard(mapResult) {
      console.log(mapResult,'mapResult')
      const cretNo = this.form.idCard.value.replace(/\s/g, "");
      const age = util.getAge(cretNo);
      const rela = this.form.rela.value;
      if (rela === 1 && age < 18) {
        return this.$dialog.alert({
          message: ",",
          width: "70vw",
          confirmButtonText: "",
          className: "back-tip"
        });
      } else if (rela === 2 && age >= 18) {
        return this.$dialog.alert({
          message: ",",
          width: "70vw",
          confirmButtonText: "",
          className: "back-tip"
        });
      }
      if (this.nextFlag) {
        const effectiveDate =
          this.effectiveDate || +new Date() + 86400000 * this.later;
        const tomorrow = new Date(effectiveDate);
        let YYYY = tomorrow.getFullYear(),
          MM = tomorrow.getMonth() + 1,
          DD =
            tomorrow.getDate() < 10
              ? "0" + tomorrow.getDate()
              : tomorrow.getDate();
        let date = YYYY + "-" + (MM < 10 ? "0" + MM : MM) + "-" + DD;
        if(mapResult.isChoice){
           date = ''
        }
        let policyInfo = { date };
        if (age < 18) {
          policyInfo = {
            insureIdCard: cretNo,
            choosedRela:  "04",
            ...policyInfo
          };
        } else {
          policyInfo = { cretNo, choosedRela: "01", ...policyInfo };
        }

        this.$store.commit("SET_DATE", {
          minDate: new Date(effectiveDate),
          maxDate: new Date(effectiveDate)
        });
        this.$store.commit("SET_USER_INFO", {
          policyInfo,
          hasSomeUserInfo: true
        });
        this.$store.commit("SET_FROM_PAGE_NAME", "card-activity-collection");
        if(this.CCBschlool){
          let CCB = {
            trainAddress: this.form.site.value, //
            studentIdentity: this.form.plan.value, //
            trainClassName: this.form.grade.value,  //
            sceneCode: this.sceneCode
          }
        this.$store.commit("SET_SCHOOL",CCB);
        }
        this.$router.replace({
          name: "card-info",
          params: {
            batchNo: mapResult.batchNo
          },
          query: this.$store.state.app.routeQuery
        });
      } else {
        let policyData = {
          plchdCrdtNo: this.form.idCard.value.replace(/\s/g, "")
        };
        if (age < 18) {
          policyData = {
            insureCertNo: this.form.idCard.value.replace(/\s/g, "")
          };
        }
        sessionStorage.setItem("elife-policyData", JSON.stringify(policyData));
        sessionStorage.setItem("fromPage", "collection");
        const urlconfig = [
          "product/product-purchase/card-activation/cardNo=/cardPwd=?batchNo=",
          "product/product-purchase/card-activation/cardNo=/cardPwd=/batchNo=",
          "product/product-purchase/card-activation-app/cardNo=/cardPwd=/batchNo="
        ];
        let bagName =
          location.origin.indexOf("yuqi56") > -1
            ? "elife-wechat-web/#/"
            : "elife-wechat/#/";
        window.location.href =
          location.origin +
          location.pathname.replace("/elife-card", "") +
          bagName +
          urlconfig[mapResult.saleType || 0] +
          mapResult.batchNo;
      }
    },
    next() {
      const cretNo = this.form.idCard.value.replace(/\s/g, "");
      const age = util.getAge(cretNo);
      const rela = this.form.rela.value;
      if (rela === 1 && age < 18) {
        return this.$dialog.alert({
          message: ",",
          width: "70vw",
          confirmButtonText: "",
          className: "back-tip"
        });
      } else if (rela === 2 && age >= 18) {
        return this.$dialog.alert({
          message: ",",
          width: "70vw",
          confirmButtonText: "",
          className: "back-tip"
        });
      }
      const BASE_URL = process.env.VUE_APP_API_BASE_URL || "";
      this.$http
        .get(
          `${BASE_URL}/statics/json/card/card-collection-json-${this.$route
            .params.id || ""}.json`
        )
        .then(
          res => {
            this.$loading.hide();
            const formKeys = Object.keys(this.form);
            let map = {};
            formKeys.forEach(v => {
              const ele = this.form[v];
              if (!ele.maps) return;
              setMap(map, ele);
            });
            const mapResult = filterMap(res, map);
            if (!mapResult)
              return this.$dialog.alert({
                message: this.noMapTip,
                width: "70vw",
                confirmButtonText: "",
                className: "back-tip"
              });
            if (+new Date() > +new Date(mapResult.endDate) + 86400000)
              return this.$dialog.alert({
                message: this.endTip,
                width: "70vw",
                confirmButtonText: "",
                className: "back-tip"
              });
            return this.goCard(mapResult);
          },
          reson => {
            console.log(reson);
          }
        );
    },
    getMap() {},
    checkForm() {
      if(!this.CCBschlool){
        this.form.grade.value = ""
      }
      const formKeys = Object.keys(this.form);
      return formKeys.find(v => {
        const ele = this.form[v];
        return !this.checkFunc({ ...ele.check, ...{ value: ele.value } }) && v;
      });
    },
    checkFunc(option) {
      const { func, required, value } = option;
      const type = typeof value;
      if (required && type === "string" && !value) {
        return false;
      }
      return !func || func(value);
    }
  },
  created() {



    this.getUserInfo();

    if(this.columns[0].type == 'block'){
      this.columns.forEach(v => {
        this.$set(this.form, v.key, v.map);
      });
    }
    if(this.area[0].type == 'select'){
      this.area.forEach(item => {
        this.$set(this.form, item.key, item.map);
      });
    }
    console.log(this.form,'form');
    // if(this.$isWechat) {
      // const currentUrl = location.origin+location.pathname;
      // console.log(currentUrl);
      // const {title="",desc="",link=location.href,imgUrl=""} = this.share || {};
      // this.shareOnWechat({currentUrl,title,desc,link,imgUrl})
    // }

  }
};
</script>

<style lang="less" scoped>
.van-popup {
  position: absolute;
  padding: 15px;
  box-sizing: border-box;
}
.columns {
  .plans-box {
    position: relative;
    padding-top: 7.5vw;
    margin-top: 10px;
    padding-bottom: 3.75vw;
    &::before {
      content: attr(data-title);
      position: absolute;
      font-size: 3.73vw;
      font-weight: 500;
      line-height: 1.5em;
      left: 0;
      top: -1px;
    }
    .plans {
      display: flex;
      width: 100%;
      flex-wrap: nowrap;
      overflow-x: auto;
      &::-webkit-scrollbar {
        background-color: transparent;
      }
      .item {
        width: 33vw;
        margin: 5px 2%;
        padding: 5px;
        box-sizing: border-box;
        font-size: 14px;
        line-height: 1.3em;
        height: calc(~"13vw + 10px");
        align-items: center;
        background-color: #edf5fe;
        color: #4ca2fe;
        font-weight: 500;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        border-radius: 2.2vw;
        &.active {
          background-color: #4ca2fe;
          color: #fff;
        }
      }
    }
  }
}
.hint{
  font-size: 12px;
  color: #F8690D;
  text-align: left;
  display: block;
}
.logo {
  display: flex;
  margin: 15px 0 10%;
  h4 {
    text-align: left;
    padding: 0;
    margin: 0;
    font-size: 4.8vw;
    font-weight: 500;
  }
}
.rela {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 12vw;
  border-bottom: 1px solid #efefef;
  .rela-title {
    font-size: 3.73vw;
  }
  .rela-content {
    span {
      display: inline-block;
      line-height: 1.5em;
      background-color: #edf5fe;
      font-size: 3.7vw;
      color: #4ca2fe;
      padding: 3px 12px;
      border-radius: 2.2vw;
      margin-left: 15px;
      &.active {
        background-color: #4ca2fe;
        color: #fff;
        font-weight: 500;
      }
    }
  }
}
.select{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 12vw;
    border-bottom: 1px solid #efefef;
  .rela-title {
    font-size: 3.73vw;
  }
  .active{
    color: #4ca2fe;
  }
}
.pop-content {
  padding-bottom: 13vw;
}
.bottom {
  width: 100%;
  position: fixed;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  background-color: #fff;
  margin: 10px auto 0;
  .next {
    height: 11vw;
    line-height: 11vw;
    font-size: 4.8vw;
  }
}
</style>
