<template>
  <div id="sharesaler">
    <div :class="['imgbox' , qrcode?'overhidden':'' ]">
      <img :src="wxShareInfo.pagebgURL" alt>
    </div>
    <div class="content">
      <div class="FJ_button_bg">
        <div class="FJ_button FJ_button1" @click="code=true"></div>
      </div>
    </div>

    <div class="zhezhao" v-show="code"></div>
    <div class="codebox" v-show="code">
      <div class="code">
        <div class="inpbox">
          <input class="inp" type="text" placeholder="" v-model="val" @blur="showshareimg('blur')">
        </div>
        <div class="shareBtn" @click="showshareimg('btn')"></div>
      </div>
      <div class="close" @click="code=false"></div>
    </div>

    <div class="zhezhao" v-show="qrcode"></div>
    <div class="qrcode" id="qrcode" v-show="qrcode">
      <div class="codeposition" id="codeposition" :style="codeStyleText"></div>
      <!-- <div class="dataDesc"></div> -->
      <img class="showDataURL" :src="DataURL" v-if="DataURL">
      <canvas id="mycanvas"></canvas>
      <!-- <img class="DataURLposition" id="DataURLposition" :src="qrcodebgURL" alt="" > -->
      <div class="close" @click="qrcode=false"></div>
      <div class="qrcodeimg" ref="qrcodeurl" id="qrcodeurl"></div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import QRCode from "qrcodejs2";
export default {
  name: "share-saler",
  components: {},
  data() {
    return {
      val: "",
      wxShareInfo: {},
      sceneCode: "",
      param: {
        currentUrl: window.location.href.split("#")[0],
        wxShareInfo: {
          title: "",
          desc: "",
          link: "",
          imgUrl: ""
        }
      },
      code: false,
      qrcode: false,

      qrcodeimg: "",
      codeStyleText: {},
      qrcodebgURL: "",
      qrcodebgWidth: "",
      DataURL: ""
    };
  },

  methods: {
    ...mapActions(["wechatShareLink"]),
    showshareimg(biubiubiu) {
      if (!this.val) {
        return this.$dialog.alert({
          message: "",
          confirmButtonText: "",
          className:"borderRadius"
        });
      }
      this.param.wxShareInfo.link = `${this.wxShareInfo.link}?sendSalerId=${this.val}&sceneCode=${this.sceneCode}`;
      console.log(this.param, "param");
      this.wechatShareLink(this.param);

      if(biubiubiu === 'btn'){
        this.code = false;
        if (this.qrcodebgWidth) {
          document.getElementById("qrcode").style.width = this.qrcodebgWidth;
        }
         this.createQrCode(); 
      }
    },
    createQrCode() {
      //
      var _this = this;
      document.getElementsByClassName("qrcodeimg")[0].innerHTML = "";
      let width = document.documentElement.clientWidth * 0.32;
      var qrcode = new QRCode(this.$refs.qrcodeurl, {
        text: _this.param.wxShareInfo.link,
        width: width,
        height: width,
        colorDark: "#000000",
        colorLight: "#ffffff"
      });
      qrcode.makeCode(this.param.wxShareInfo.link);
      console.log(this.param.wxShareInfo.link);
      this.qrcode = true;

      // setTimeout(()=>{
      //     _this.beginDraw();
      // },200)
       _this.beginDraw();
    },
    beginDraw() {
      //
      var _this = this;

      var mycanvas = document.getElementById("mycanvas");
      var context = mycanvas.getContext("2d");

      const pixelRatio = window.devicePixelRatio || 1
      const backingStore = context.backingStorePixelRatio || 
                          context.webkitBackingStorePixelRatio ||
                          context.mozBackingStorePixelRatio ||
                          context.msBackingStorePixelRatio ||
                          context.oBackingStorePixelRatio ||
                          context.webkitBackingStorePixelRatio || 1;
      const ratio = pixelRatio / backingStore

      //
      var imgCode = new Image();
      imgCode.onload = () => {
        var codeDOM = document.getElementById("codeposition");
        var codeLeft = codeDOM.offsetLeft * ratio;
        var codeTop = codeDOM.offsetTop * ratio;
        var codeWidth = codeDOM.offsetWidth * ratio;
        var codeHeight = codeDOM.offsetHeight * ratio;
        
        //
        var imgBg = new Image();
        imgBg.onload = function() {
          if (document.getElementById("DataURLposition")){document.getElementById("DataURLposition").remove();}
          imgBg.setAttribute("id", "DataURLposition");
          imgBg.style.width = "100%";
          imgBg.style.position = "absolute";
          imgBg.style.left = "10000px";

          document.getElementById("qrcode").appendChild(imgBg);
          var canvansDOM = document.getElementById("DataURLposition");
          var canvansWidth = canvansDOM.offsetWidth * ratio;
          var canvansHeight = canvansDOM.offsetHeight * ratio;

          mycanvas.width = canvansWidth;
          mycanvas.height = canvansHeight;

          context.drawImage(imgBg, 0, 0, canvansWidth, canvansHeight);
          context.drawImage(imgCode, codeLeft, codeTop, codeWidth, codeHeight);

          let clientWidth = document.documentElement.clientWidth;
          let fontWidth = (clientWidth * 16) / clientWidth*ratio;
          let fonttop = (clientWidth * 8) / clientWidth*ratio;
          context.font = fontWidth + "px sans-serif";
          context.fillStyle = "rgba(255,255,255,1)";
          context.textAlign = "center";

          context.fillText( _this.val, codeLeft + codeWidth / 2, codeTop - fonttop, codeWidth );

          _this.DataURL = mycanvas.toDataURL("image/png");
        };
        imgBg.src = this.qrcodebgURL || this.wxShareInfo.pagebgURL;
      };
      imgCode.src = document.getElementById("qrcodeurl").children[0].toDataURL("image/png");
    }
  },
  mounted() {},
  created() {
    const BASE_URL = process.env.VUE_APP_API_BASE_URL || "";
    this.$http.get(`${BASE_URL}/statics/json/salerWXShare.json`).then(res => {
      this.$loading.hide();
      for (let k in res.cardwxShare) {
        if (k == this.$route.params.KDPCNO) {
          this.wxShareInfo = res.cardwxShare[k].wxShareInfo;
          this.sceneCode = res.cardwxShare[k].wxShareInfo.sceneCode;
          
          this.qrcodebgURL = res.cardwxShare[k].qrcodebgURL;
          if (res.cardwxShare[k].codeStyleText)
            this.codeStyleText = res.cardwxShare[k].codeStyleText;
          if (res.cardwxShare[k].qrcodebgWidth)
            this.qrcodebgWidth = res.cardwxShare[k].qrcodebgWidth;
        }
      }
      console.log(this.wxShareInfo, "info");
      this.param.wxShareInfo.link = this.wxShareInfo.link;
      this.param.wxShareInfo.title = this.wxShareInfo.title;
      this.param.wxShareInfo.desc = this.wxShareInfo.desc;
      this.param.wxShareInfo.imgUrl = this.wxShareInfo.imgUrl;
      console.log(this.param, "param");
      this.wechatShareLink(this.param);
    });
  }
};
</script>

<style scoped >
