  left: 100000px;
}
.zhezhao {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #222;
  /* background: rgba(0, 0, 0, 1); */
}
.codeposition {
  width: 21.5vw;
  height: 21.5vw;
  position: absolute;
  top: 104vw;
  right: 29vw;
  z-index: -1;
}
.qrcodeimg {
  position: absolute;
  left: 800000px;
}

.overhidden {
  height: 100vh;
  overflow: hidden;
}
