      this.childrenData[e.selectPolicyIndex].form = e.selectedPerson;
      this.$set(this.childrenData[e.selectPolicyIndex].form, "disabled", true);
      console.log(e, this.childrenData);
    },
    async handleConfirm() {
      const submitPromises = this.childrenData.map((_, index) => {
        return new Promise((resolve) => {
          this.$refs[`child${index}`][0].$refs.form.submit(); // 
          const isonSubmitCheck = setInterval(() => {
            if (this.$refs[`child${index}`][0].isonSubmit !== undefined) {
              clearInterval(isonSubmitCheck); // 
              resolve(this.$refs[`child${index}`][0].isonSubmit); //  isonSubmit 
            }
          }, 100); // 100ms
        });
      });

      const results = await Promise.all(submitPromises); // 

      const allValid = results.every((result) => result === true); //  true
      if (allValid) {
        console.log("");
        // 
      } else {
        console.log("", results);
        // 
      }
    },

