<template>
  <Layout @next="next">
    <template v-slot:pop>
      <slotPopup v-model="showPop"></slotPopup>
    </template>
  </Layout>
</template>

<script>
import Layout from "../layout";
import slotPopup from "../popup/";
import { mapActions  } from "vuex";
export default {
  components: {
    Layout,
    slotPopup
  },
  data() {
    return {
      showPop: false,
    };
  },
  methods: {
    ...mapActions([ "wechatShareLink"]),
    next() {
      this.showPop = true;
    }
  },
  created(){
    this.$store.commit("SET_SEND_SALER_ID",this.$route.query.sendSalerId || '')
    this.$store.commit("SET_SCENE_CODE",this.$route.query.sceneCode || '')
    const BASE_URL = process.env.VUE_APP_API_BASE_URL || "";
    this.$http.get(`${BASE_URL}/statics/json/salerWXShare.json`).then(res => {
      this.$loading.hide();
      for(let k in res.cardwxShare){
          if(k==this.$route.params.id){
            var wxShareInfo=res.cardwxShare[k].wxShareInfo
            let param= {
                currentUrl: window.location.href.split("#")[0],
                wxShareInfo: {
                  title: wxShareInfo.title,
                  desc: wxShareInfo.desc,
                  link: window.location.href,
                  imgUrl: wxShareInfo.imgUrl
                }
              }
            console.log(param,"param")
            this.wechatShareLink(param);
          }
      }
    })
  }
};
</script>

<style lang="less" scoped >
</style>
